
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
created(){
this.util.init(this);
this.query("query")
},
activated(){
this.util.unmask();
},
deactivated(){
this.$destroy(true)
},
methods:{
query(state){
let Id = this.$route.query.Id;
let xmid = this.$route.query.xmid;
var _this = this;
if(state ==="init"){
if(this.$route.query.AddName !=this.sessionUtil.getUserId()){
this.util.alert("您没有操作权限！");
return;
}
}
this.util.post("/admin/appAgentAction!runAgent.action",{
agentId:"Agent_WeChat_Project_Init_Report",
type:"projectReport",
projectReportId:Id,
xmid,
state
},function(res){
_this.planList = res.data;
if(res.status == true){
_this.planList = res.data;

}
})
},
edit(Id){
this.util.gotoByName("prgReportDetail",{Id:Id})
},
cancelCallback() {
if (this.validUtil.isNotEmpty(this.Id)) {
this.$router.back(-1);
} else {

history.back();

}
}
},
data(){
return{
planList:[
]
}
},
components:{
Group,CellBox,Icon,XButton,
Flexbox,
FlexboxItem
}
}



